.warpper__mobile__nav {
  position: fixed;
  display: none;
  width: 70%;
  top: 0;
  left: 0;
  bottom: 0;
  background-color: #fff;
  z-index: 99;
  overflow-y: auto;
  animation: showNavbarMobile ease 300ms;
  .warpper__mobile__nav__content {
    background-color: #fff;
    .wrapper__loign__signup {
      width: 100%;
      padding: 8px 16px;
      font-weight: 700;
      text-align: left;
      color: $color_medium_blue;
      background-color: $color_white;
      border: none;
    }
    .warpper__userAcount {
      background-color: #fbfbf8;
      padding: 10px;
      @include alignCenter;
      justify-content: space-between;
      .warpper__user__info {
        @include alignCenter;
        justify-content: space-between;
        .image__avarta {
          position: relative;
          img {
            max-width: 64px;
            max-height: 64px;
            border-radius: 50%;
          }
          .circleRed {
            position: absolute;
            top: 0;
            right: 7%;
            @include sizeImg(15px, 15px);
            border-radius: 50%;
            background-color: $color_red;
          }
        }
        .userAccount {
          padding-right: 55px;
          .nameUser {
            @include text($color_black-11, 0.9rem, bold);
            word-break: break-word;
          }
          .welcome {
            margin: 0;
            @include text(#73726c, 0.9rem, normal);
          }
        }
      }

      .arrow {
        i {
          @include text($color_black-11, 20px, bold);
        }
      }
    }
    .warrper__list__content {
      .list__header {
        padding: 10px 15px 0 15px;
        @include text($color-gray-4, 14px, bold);
        text-transform: uppercase;
        letter-spacing: 1px;
        margin: 0;
        border-top: 1px solid $color-gray-7;
      }

      .list__category {
        padding: 8px 16px;

        .list__categpry__item {
          display: flex;
          justify-content: space-between;
          padding: 10px 0;
          .category {
            @include text($color-gray-4, 14px, normal);
          }
          .fa-angle-right {
            @include text($color_black-11, 20px, bold);
          }
        }
      }
    }
  }
  .warpper__mobile__nav__sideDrawer {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 200vh;
    background-color: #fff;
    z-index: 10;
    animation: showSideBarDrawer ease 400ms;
    .header__title__sideDrawer {
      background-color: #fbfbf8;
      padding: 15px;
      i {
        @include text($color_black-11, 15px, bold);
      }
      .textMenu {
        margin-left: 10px;
        @include text($color_black-11, 15px, normal);
      }
    }

    .list__mobile {
      .list__header__sideDrawer {
        @include text($color-gray-4, 15px, bold);
        padding: 10px 15px;
        margin: 0;
        border-top: 1px solid $color-gray-7;
      }

      .list__category__sideDrawer {
        padding: 0 15px;

        .list__categpry__item {
          display: flex;
          justify-content: space-between;
          padding: 10px 0;
          .category {
            @include text($color-gray-4, 14px, normal);
          }
          .fa-angle-right {
            @include text($color_black-11, 20px, bold);
          }
        }
      }
    }
  }

  @keyframes showSideBarDrawer {
    from {
      right: -100%;
    }
    to {
      right: 0;
    }
  }
}

@media only screen and (min-width: 740px) and (max-width: 1024px) {
  .overPlay,
  .warpper__mobile__nav {
    display: block;
    .warpper__mobile__nav__content {
      .warpper__userAcount {
        .userAccount {
          padding-right: 50%;
        }
      }
    }
  }
}

@media only screen and (max-width: 740px) {
  .overPlay,
  .warpper__mobile__nav {
    display: block;
  }
}
